<template>
  <div class="reset-psw-view view-background">
    <header class="search-view-top"><div class="search-back" @click="back"></div>
      <div class="search-bar">
        <div class="search-bar-tool">
          <form action="" @submit.prevent="search">
            <input type="search" placeholder="搜索城市名" v-model="cityName">
          </form>
        </div>
      </div>
    </header>
    <section class="scroll-contaier">
      <div class="below-head">
        <span>当前城市</span>
        <div>
          <span class="this-city" @click="isCurrCheck()">
          <i class="position-icon"></i>{{city}}
        </span>
        </div>
      </div>
        <div class="city-box">
          <div class="city-list" v-for="(item,key) in cityList" v-if="item.length != 0">
            <span>{{key}}</span>
            <div class="city-item" :class="{'check':city == item1.name}" @click="isCheck(item1.name)" v-for="(item1,key1) in item">{{item1.name}}
              <i class="select-city-icon" :class="{'select-city-icon-block':city == item1.name}"></i></div>
          </div>
        </div>
    </section>
  </div>
</template>

<script>
  import {getAllCity} from '../../service/getData'
  import BMap from 'BMap'
  export default {
    data () {
      return {
        city: '',
        fixed: false,
        routerName: 'index',
        cityList: '',
        cityName: ''
      }
    },
    created: function () {
      let _this = this
      /* 定位当前城市  */
      let myCity = new BMap.LocalCity()
      myCity.get(res => {
        _this.city = res.name
      })
      getAllCity()
        .then(function (rep) {
          _this.cityList = rep.data
        })
        .catch(function () {
          _this.$toast('获取城市信息失败！')
        })
    },
    methods: {
      search: function () {
        // 按城市名称搜索
        let _this =this
        getAllCity(this.cityName)
        .then(res => {
          _this.cityList = res.data
        })
      },
      isCheck: function (val) {
        this.city = val
        this.sendBtnTimer = setInterval(this.time, 500)
      },
      isCurrCheck: function(){
        //点击当前城市跳转
        this.sendBtnTimer = setInterval(this.time, 500)
      },
      time: function () {
        clearInterval(this.sendBtnTimer)
        this.$router.push({
          name: 'home',
          query: {
            city: this.city
          }
        })
      },
      back: function () {
        this.$router.push({
          name: 'home'
        })
      }
    },
    watch: {
      cityName: function () {
        // 按城市名称搜索
        let _this =this
        getAllCity(this.cityName)
        .then(res => {
          _this.cityList = res.data
        })
      }
    }
  }
</script>

<style>
/* 选择城市 */
.reset-psw-view header.search-view-top {
  padding-left: .15rem;
  height: .4rem;
  line-height: .4rem;
  display: flex;
  background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
  /* Safari 5.1 - 6.0 */
  background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
  /* 标准的语法（必须放在最后） */
}
.scroll-contaier{
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.below-head{
  padding: .2rem 0 0rem .15rem;
}
.this-city{
  height: .28rem;
  line-height: .28rem;
  background-color: white;
  color: #333;
  display: inline-block;
  padding: 0rem .15rem 0rem .15rem;
  border: .01rem solid rgba(0,0,0,.1);
  border-radius: .2rem;
    margin-top: .14rem;
}
.position-icon{
  width: .15rem;
  height: .15rem;
  display: inline-block;
  background: url(/static/image/select_city_now_location@3x.png) no-repeat;
  background-size: 100% 100%;
  position: relative;
    top: .02rem;
    left:-.02rem;
}
.city-box{
  margin-top: .08rem;
  padding-bottom: .65rem;
}
.city-list{
  /*margin-top: .08rem;*/
}
.city-list>span{;
  line-height: .3rem;
  margin-left: .15rem;
}
.city-item{
  padding-left: .15rem;
  height: .5rem;
  line-height: .49rem;
  background-color: white;
  position: relative;
}
.city-item:before{
  width: 100%;
  height: .01rem;
  content: "";
  position: absolute;
  bottom: 0;
  border-bottom: .01rem solid rgba(0,0,0,.1);
}
.city-item:last-child:before{
  border-bottom: none;
}
.select-city-icon{
  width: .16rem;
  height: .16rem;
  background: url(/static/image/select_city_list_select@3x.png) no-repeat;
  background-size: 100% 100%;
  float: right;
  display: none;
  position: relative;
  top: 50%;
  transform: translate(-100%,-50%);
}
.select-city-icon-block{
  display: inline-block;
}
.settle-accounts-box{
  bottom: .55rem;
}
.check{
  color: #ff8b20;
}
.search-bar{
  padding-right: .15rem;
}
.search-back{
  margin-top: .05rem;
  width: .3rem;
  height: .3rem;
  background: url(/static/image/classify/classify_back@3x.png);
  background-size: 100% 100%;
}
form {
  width: 100%;
  height: 100%;
}
</style>


